import React from "react"
import {Link} from "gatsby";
import styles from "../styles/toolbar.module.css";
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

// import SubHeader from "../components/subheader.js"
export const ListLink = props => (
    <Link to={props.to} className={styles.icon}>
        <img src={props.iconpath} className={styles.iconimg} alt=""/>
    </Link>
)

export default props => (
    <DropLink to={props.to} className={styles.icon}>
        <img src={props.iconpath} className={styles.iconimg} alt=""/>
    </DropLink>
)

class DropLink extends React.Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.onMouseEnter = this.onMouseEnter.bind(this);
        this.onMouseLeave = this.onMouseLeave.bind(this);
        this.state = {
            dropdownOpen: false
        };
        this.to = props.to;
        this.children = props.children;
    }

    toggle() {
        this.setState(prevState => ({
            dropdownOpen: !prevState.dropdownOpen
        }));
    }

    onMouseEnter() {
        this.setState({dropdownOpen: true});
    }

    onMouseLeave() {
        this.setState({dropdownOpen: false});
    }

    render() {
        return (
            <Dropdown
                // onMouseOver={this.onMouseEnter} onMouseLeave={this.onMouseLeave}
                isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                <DropdownToggle className={styles.icon}>
                    <Link to={this.to}>
                        {this.children}
                    </Link>
                </DropdownToggle>
                <DropdownMenu className={styles.subheader}>
                    <DropdownItem><Link to={this.to}>A
                    </Link></DropdownItem>
                    <DropdownItem><Link to={this.to}>B
                    </Link></DropdownItem>
                    <DropdownItem><Link to={this.to}>C
                    </Link></DropdownItem>
                    <DropdownItem><Link to={this.to}>D
                    </Link></DropdownItem>
                </DropdownMenu>
            </Dropdown>
        );
    }
}